"use client";
import React, { useState, useEffect, ReactNode, useMemo } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import {
  YadakchiAdminLayout,
  ListPageLayout,
  Label,
  Badge,
  FormModal,
  Input,
  Button,
  ToggleSwitch,
  Select, // ุงููพูุฑุช Select
  type ColumnDefinition,
  type SelectOption,
} from "@my-ds/design-system";

import {
  getTicketCategories,
  createTicketCategory,
  updateTicketCategory,
} from "../../services/api/ticketCategoryService";
import { TicketCategory } from "@/services/api/types";
import { exportToExcel } from "../../utils/exportUtils";

// ุฏุงุฏูโูุง ููุชุฑ ูุถุนุช ูุฎุชุต ุฏุณุชูโุจูุฏ ุชฺฉุช
const categoryStatusFilters: SelectOption[] = [
  { value: "all", label: "ููู ูุถุนุชโูุง" },
  { value: "true", label: "ูุนุงู" },
  { value: "false", label: "ุบุฑูุนุงู" },
];

export default function TicketCategoryPage() {
  const [allCategories, setAllCategories] = useState<TicketCategory[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const [isFormModalOpen, setIsFormModalOpen] = useState(false);
  const [editingCategory, setEditingCategory] = useState<TicketCategory | null>(
    null
  );
  const [formData, setFormData] = useState({
    name: "",
    description: "",
    isActive: true,
  });
  const [actionLoading, setActionLoading] = useState(false);
  const [isExporting, setIsExporting] = useState(false);

  // State ุจุฑุง ููุชุฑ ูุถุนุช
  const [selectedStatus, setSelectedStatus] = useState("all");

  const pathname = usePathname();
  const renderLink = (href: string, children: ReactNode) => (
    <Link href={href}>{children}</Link>
  );

  const fetchCategories = async () => {
    try {
      setIsLoading(true);
      const response = await getTicketCategories();
      setAllCategories(response);
    } catch (err) {
      setError(err instanceof Error ? err.message : "ุฎุทุง");
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    fetchCategories();
  }, []);

  // ููุทู ููุชุฑ ฺฉุฑุฏู ุณูุช ฺฉูุงูุช
  const filteredCategories = useMemo(() => {
    if (selectedStatus === "all") {
      return allCategories;
    }
    const isActive = selectedStatus === "true";
    return allCategories.filter((category) => category.isActive === isActive);
  }, [allCategories, selectedStatus]);

  // --- Handlers ---
  const handleOpenAddModal = () => {
    setEditingCategory(null);
    setFormData({ name: "", description: "", isActive: true });
    setIsFormModalOpen(true);
  };

  const handleOpenEditModal = (category: TicketCategory) => {
    setEditingCategory(category);
    setFormData({
      name: category.name,
      description: category.description,
      isActive: category.isActive,
    });
    setIsFormModalOpen(true);
  };

  const handleFormSubmit = async () => {
    if (!formData.name.trim()) {
      alert("ูุงู ุฏุณุชูโุจูุฏ ููโุชูุงูุฏ ุฎุงู ุจุงุดุฏ.");
      return;
    }
    setActionLoading(true);
    try {
      if (editingCategory) {
        await updateTicketCategory(editingCategory.id, {
          Name: formData.name,
          Description: formData.description,
          IsActive: formData.isActive,
        });
      } else {
        await createTicketCategory({
          Name: formData.name,
          Description: formData.description,
        });
      }
      setIsFormModalOpen(false);
      await fetchCategories();
    } catch (err) {
      alert("ุฎุทุง ุฏุฑ ุฐุฎุฑูโุณุงุฒ.");
    } finally {
      setActionLoading(false);
    }
  };

  const handleDelete = async (categoryId: string) => {
    if (window.confirm("ุขุง ุงุฒ ุญุฐู ุงู ุฏุณุชูโุจูุฏ ูุทูุฆู ูุณุชุฏุ")) {
      // await deleteTicketCategory(categoryId); // ุชุงุจุน API ุจุงุฏ ุณุงุฎุชู ุดูุฏ
      await fetchCategories();
    }
  };

  const handleExport = async () => {
    if (!filteredCategories.length) return;
    setIsExporting(true);
    await new Promise((resolve) => setTimeout(resolve, 500));
    const dataToExport = filteredCategories.map((c) => ({
      ุดูุงุณู: c.id,
      ูุงู: c.name,
      ุชูุถุญุงุช: c.description,
      ูุถุนุช: c.isActive ? "ูุนุงู" : "ุบุฑูุนุงู",
    }));
    exportToExcel(dataToExport, "ูุณุช_ุฏุณุชูโุจูุฏโูุง");
    setIsExporting(false);
  };

  const categoryColumns: ColumnDefinition<TicketCategory>[] = [
    {
      accessorKey: "id",
      header: "ุฑุฏู",
      cell: (category, index) => <Label text={String(index + 1)} size="md" />,
    },
    {
      accessorKey: "name",
      header: "ูุงู ุฏุณุชูโุจูุฏ",
      cell: (c) => <Label text={c.name} size="md" />,
    },
   { 
      accessorKey: 'description', 
      header: 'ุชูุถุญุงุช', 
      // โจ --- ุชุบุฑ ฺฉูุฏ ุงูุฌุงุณุช ---
      cell: (c) => <Label text={c.description} size="md" variant="secondary" truncate /> 
    },
    {
      accessorKey: "isActive",
      header: "ูุถุนุช",
      cell: (c) => (
        <Badge
          label={c.isActive ? "ูุนุงู" : "ุบุฑูุนุงู"}
          variant={c.isActive ? "success" : "neutral"}
        />
      ),
    },
  ];

  const filterSection = (
    <div style={{ display: "flex", justifyContent: "flex-start" }}>
      <Select
        options={categoryStatusFilters}
        value={selectedStatus}
        onChange={(e) => setSelectedStatus(e.target.value)}
        aria-label="ููุชุฑ ุจุฑ ุงุณุงุณ ูุถุนุช"
      />
    </div>
  );

  const listPageProps = {
    listPageHeaderProps: {
      title: "ูุฏุฑุช ุฏุณุชูโุจูุฏ ุชฺฉุชโูุง",
      totalItems: filteredCategories.length,
      exportButtonProps: {
        onClick: handleExport,
        isLoading: isExporting,
      },
      actions: <Button onClick={handleOpenAddModal}>ุงูุฒูุฏู ุฏุณุชูโุจูุฏ</Button>,
    },
    tableProps: {
      columns: categoryColumns,
      data: filteredCategories,
      emptyStateMessage: "ูฺ ุฏุณุชูโุจูุฏ ุงูุช ูุดุฏ.",
      rowActions: (category: TicketCategory) => ({
        onEdit: () => handleOpenEditModal(category),
        // onDelete: () => handleDelete(category.id),
      }),
    },
    filterSection: filterSection,
    isLoading: isLoading,
    error: error,
  };

  return (
    <>
      <YadakchiAdminLayout currentPath={pathname} renderLink={renderLink}>
        <ListPageLayout {...listPageProps} />
      </YadakchiAdminLayout>

      <FormModal
        isOpen={isFormModalOpen}
        onClose={() => setIsFormModalOpen(false)}
        title={editingCategory ? "ูุฑุงุด ุฏุณุชูโุจูุฏ" : "ุงูุฒูุฏู ุฏุณุชูโุจูุฏ ุฌุฏุฏ"}
        onConfirm={handleFormSubmit}
        isLoading={actionLoading}
        confirmText={editingCategory ? "ุฐุฎุฑู" : "ุงุฌุงุฏ"}
      >
        <form
          onSubmit={(e) => {
            e.preventDefault();
            handleFormSubmit();
          }}
          style={{ display: "flex", flexDirection: "column", gap: "1.5rem" }}
        >
          <Input
            id="category-name"
            label="ูุงู ุฏุณุชูโุจูุฏ"
            value={formData.name}
            onChange={(e) =>
              setFormData((prev) => ({ ...prev, name: e.target.value }))
            }
            required
            autoFocus
          />
          <Input
            id="category-description"
            label="ุชูุถุญุงุช"
            value={formData.description}
            onChange={(e) =>
              setFormData((prev) => ({ ...prev, description: e.target.value }))
            }
          />
          {editingCategory && (
            <ToggleSwitch
              label="ุฏุณุชูโุจูุฏ ูุนุงู ุจุงุดุฏ"
              enabled={formData.isActive}
              onChange={(enabled) =>
                setFormData((prev) => ({ ...prev, isActive: enabled }))
              }
            />
          )}
        </form>
      </FormModal>
    </>
  );
}
